* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-info__title {
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 0;
}

.country-info__data {
  margin: 15px 0 0;
}

.country-info__subtitle {
  font-weight: 700;
}

.country-list {
  list-style-type: none;
  padding: 0;
  margin: 20px 0 0;
}

.country-list__item {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 15px 0 0;
}

.country-list__item:first-child {
  margin-top: 0;
}

.country-list__title {
  margin: 0;
  font-size: 20px;
}
